body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html, body {
  text-align: center;
  background: 
  linear-gradient(
      rgba(0, 0, 0, 0.25), 
      rgba(0, 0, 0, 0.25)
    ),
  url(/fitnessstore/static/media/background.586509d0.jpg) no-repeat center center fixed;
  background-size: cover;
}

.Grid {
  margin: 5%;
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
}

.static-logo {
  height: 150px;
  cursor: pointer;
}

.menu {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  top: 145px;
  left: 0;
  right:0;
  font-weight: 800;
  font-size: 35px;
  text-shadow: 0px 0px 5px rgba(0,0,0,0.42);
  cursor: pointer;
}

.menu-hidden {
  visibility: hidden;
}

.App-header {
  height: 150px;
  padding: 20px;
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.6s;
  width: 30%;
  height: 400px;
  background-color: white;
  margin: 5px 5px 15px 5px;
  border-radius: 10px;
  cursor: pointer;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.card-flip {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.front, .back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  height: 480px;
}

.front {
  z-index: 2;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.price {
  color: red;
  font-weight: 800;
  font-size: 35px;
  text-shadow: 0px 0px 5px rgba(0,0,0,0.42);
}

.container {
  padding: 2px 16px;
  background-color: white;
  border-radius: 0 0 10px 10px;
}

.container-back {
  padding: 2px 16px;
  background-color: white;
  border-radius: 10px 10px 10px 10px;
}

.card-image {
  width: 50%;
  border-radius: 10px 10px 0 0;
  margin-top:20px;
 
}
a{
  color: #000;
}
a:visited {
  text-decoration: none;
  color:#000;
}
.back_button::before{
  content: "<-   ";
  font-weight: 100;
}
.back_button {
  text-decoration: none;
  color:#fff !important; 
  font-weight: 600;
  font-size: 30px;
}
.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  background-color: black;
  overflow-x: hidden;
  transition: 0.5s;
}

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: white;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
  cursor: pointer;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }
  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}
